import StyleSheets from "./style/networkScoreLayout.module.css";

const NetworkScoreLayout = (props) => {

    const network_scores = props.data.network_scores;
    console.log("network_scoresn", network_scores[0].persons);

    return (
        <div className={`${StyleSheets.all} container mt-5`} style={{ margin: 'auto', textAlign: 'center' }}>

            <div className={`row text-center `}>
                <div className={`${StyleSheets.child} col-10 row`} style={{ direction: 'rtl' }}>
                    {network_scores.map((el, index) => {
                        return (
                            <div className={`${StyleSheets.child} ml-1`} style={{ display: 'flex', borderRadius: '50%', height: '50px', width: '50px', backgroundColor: '#f5f5fa', border: 'solid 3px #1792c7' }}>
                                <span style={{ fontSize: '15px', fontFamily: 'IRANSansFN', color: '#1792c7' }} className="text-center">
                                    {el.persons}
                                </span>
                            </div>
                        )
                    })}
                </div>
                <div className="col-2 mt-2">
                    <img className={StyleSheets.point} />
                </div>
            </div>

            <div className="row text-center mt-3">
                <div className="col-10 row" style={{ direction: 'rtl' }}>
                    {network_scores.map((el, index) => {
                        return (
                            <div className="p-1 " style={{ backgroundColor: '#1f7aaa' }}>
                                <span style={{ fontSize: '15px', fontFamily: 'IRANSansFN', color: 'white' }} className="text-center">
                                    {el.label}
                                </span>
                            </div>
                        )
                    })}
                </div>
                <div className="col-2 mt-2">
                    <img className={StyleSheets.level} />
                </div>
            </div>

        </div>
    )
}

export default NetworkScoreLayout;